import React from 'react';

//import searchIcon from '/static/icons8-search_filled.png';

const navBar = (props) => (
    
    <div className="nav-bar">
       <div style={{backgroundColor: "white", height:"100%", width: "950px"}}>
            <div style={{marginLeft:"40px", height:"100%", width: "100px", display: "inline-block", marginTop:"10px", verticalAlign: "center"}}>
                <img src="https://vignette1.wikia.nocookie.net/custombionicle/images/c/c0/YouTube.png/revision/latest?cb=20120916061301" alt="logo" style={{maxWidth: "80%", maxHeight:"80%"}}/>
            </div>
            <input onChange={props.changed} className="search-bar" type="text" id="searchquery" placeholder="Search" />
            <button onClick={props.click} className="search-button" type="button"><img style={{marginTop: "2px", maxWidth: "80%", maxHeight:"80%"}} src='data:image/png;base64,'/></button>
       </div>
     <style jsx>{`
            .nav-bar {
                width: 100%;
                height: 50px;
                margin-bottom: 30px;
                box-shadow: 0 2px 3px #ccc;
            }

            .search-bar {
                font-size: 16px;
                vertical-align: top;
                margin-left: 40px;
                height: 30px;
                margin-top: 8px;
                margin-bottom: 12px;
                width: 600px;
                display: inline-block;
            }

            .search-button {
                vertical-align: top;
                height: 36px;
                margin-top: 8px;
                margin-bottom: 6px;
                width: 70px;
                display: inline-block;
            }

            .hamburger-icon {
                object-fit: cover;
                position: relative;
                top: 50%;
                transform: translateY(-50%);
                display: inline-block;
            }
        `}
        </style>
    </div>
    
);

export default navBar;


